
#app {
  font-family: Arial, Helvetica, sans-serif;
}
h1 {
  font-size: 48px;
  font-weight: normal;
  margin-bottom: 30px;
  text-align: center;
}
.row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  max-width: 900px;
}
.row .column {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 50%;
            flex: 1 0 50%;
    padding: 0 10px;
}
.fighters {
  margin-bottom: 50px;
}

.button {
  cursor: pointer;
  outline: none;
}
.button.red {
    border: 5px solid red;
    border-radius: 50%;
    background: -webkit-gradient(linear, left top, right bottom, from(darkred), to(red));
    background: linear-gradient(to bottom right, darkred, red);
    height: 80px;
    width: 80px;
}
.button.green {
    border: 5px solid green;
    border-radius: 50%;
    background: -webkit-gradient(linear, left top, right bottom, from(darkgreen), to(green));
    background: linear-gradient(to bottom right, darkgreen, green);
    height: 80px;
    width: 80px;
}
.button.gold {
    border: 5px solid gold;
    border-radius: 50%;
    background: -webkit-gradient(linear, left top, right bottom, from(goldenrod), to(gold));
    background: linear-gradient(to bottom right, goldenrod, gold);
    height: 80px;
    width: 80px;
}
.button.plain {
    background: #ccc;
    padding: 10px 20px;
    text-align: center;
}

h2 {
  font-size: 18px;
  margin-bottom: 15px;
}
.message-log {
  margin: 0;
  padding: 0;
}
.message-log li {
    background: #eee;
    list-style: none;
    font-size: 10px;
    margin-bottom: 5px;
    padding: 5px;
}

.progress-bar {
  background: #eee;
  border: 1px solid #aaa;
  height: 15px;
}
.progress-bar .progress-amount {
    background: green;
    height: 100%;
    -webkit-transition: .5s ease all;
    transition: .5s ease all;
}

.overlay {
  background: rgba(190, 0, 0, 0.6);
  left: 0;
  height: 100%;
  position: fixed;
  top: 0;
  width: 100%;
}
.overlay .modal {
    background: white;
    -webkit-box-shadow: 0 0 10px darkred;
            box-shadow: 0 0 10px darkred;
    left: 50%;
    max-width: 600px;
    margin: 0 auto;
    min-height: 100px;
    padding: 40px;
    position: fixed;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.overlay .modal .modal-close {
      font-size: 24px;
      position: absolute;
      right: 10px;
      top: 10px;
}
